@font-face {
  font-family: "Poppins-Italic";
  src: url("./fonts/Poppins-Italic.ttf");
}

body {
  background-color: hsl(0, 0%, 94%);
}

section {
  margin: 0 auto;
  width: 480px;
  padding: 25px;
  background-color: hsl(0, 0%, 100%);
  border-radius: 20px 20px 40% 20px;
}

form {
  display: flex;
}

form > label,
label::placeholder {
  width: 100px;
  display: flex;
  flex-direction: column;
  margin: 0 10px 0 10px;
  color: hsl(0, 1%, 44%);
  font-size: 18px;
  font-family: "Poppins-Italic";
  font-weight: 400;
}

input {
  padding: 6px;
  border-radius: 5px;
  border-style: solid;
  border-width: 1px;
  border-color: hsl(0, 0%, 86%);
  text-align: center;
  font-family: url("./fonts/Poppins-Bold.ttf");
}

div > p > bold {
  font-size: 38px;
  font-family: fantasy;
  font-weight: 800;
  color: hsl(259, 100%, 65%);
}

div > p {
  margin: 0 0 0 0;
  font-family: fantasy;
  font-size: 38px;
  font-weight: 900;
}

#calculate {
  position: relative;
  width: 70px;
  height: 70px;
  right: -370px;
  top: 0px;
  margin: 0;
  border-radius: 50%;
  border-width: 0;
  background-color: hsl(259, 100%, 65%);
  z-index: 200;
}

hr {
  position: fixed;
  width: 420px;
  top: 125px;
  z-index: 100;
}

.hide-content {
  display: none;
  font-size: 10px;
}

.error {
  color: hsl(0, 100%, 67%);
}

.error-border {
  border-color: hsl(0, 100%, 67%);
}

.attribution {
  position: absolute;
  bottom: 0px;
}

@media (max-width: 768px) {
  #container {
    width: 260px;
    border-radius: 20px 20px 35% 20px;
  }

  #verified {
    width: 200px;
  }

  #verified > label {
    width: 70px;
  }

  hr {
    width: 250px;
    top: 148px;
  }

  #calculate {
    position: relative;
    left: 100px;
    margin-top: 18px;
    margin-bottom: 25px;
  }

  #verified > input {
    margin-bottom: 10px;
  }
}
